@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700;800;900&display=swap");
*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #f3f2f1;
  font-family: "Raleway", sans-serif;
  font-weight: 400;
  line-height: 1.5;
}

::selection {
  background-color: #ff9800;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1170px;
  margin: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.justify-content-between {
  justify-content: space-between;
}

.align-items-center {
  align-items: center;
}

.section-title {
  flex: 0 0 100%;
  max-width: 100%;
  margin-bottom: 60px;
  padding: 0 15px;
}
.section-title h1 {
  display: inline-block;
  text-transform: capitalize;
  font-size: 40px;
  color: #000;
  font-weight: 700;
  position: relative;
}
.section-title h1::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 80px;
  height: 3px;
  background-color: #ff9800;
}

.btn-01 {
  background-color: #ff9800;
  color: #fff;
  border: 2px solid transparent;
  padding: 10px 35px;
  border-radius: 30px;
  line-height: 1.5;
  cursor: pointer;
  font-weight: 600;
  display: inline-block;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.btn-01:hover {
  background-color: transparent;
  border-color: #ff9800;
  color: #ff9800;
}
.btn-01:focus {
  outline: none;
}

/**** HOME SECTION ****/
.home-section {
  min-height: 100vh;
  background-image: url("/src/img/home-bg.jpg");
  background-position: center;
  background-size: center;
  position: relative;
  z-index: 1;
}
.home-section::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #000;
  opacity: 0.7;
  z-index: -1;
}
.home-section .row {
  min-height: 100vh;
}
.home-section .home-content {
  width: 100%;
  max-width: 100%;
  text-align: center;
}
.home-section .home-content h4 {
  font-size: 20px;
  color: #fff;
}
.home-section .home-content h1 {
  font-size: 60px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 900;
  letter-spacing: 10px;
}

/**** ABOUT SECTION ****/
.about-section {
  padding: 80px 0 0;
}
.about-section .about-img {
  flex: 0 0 33.33%;
  max-width: 33.33%;
  padding: 0 15px;
}
.about-section .about-img .img-box {
  position: relative;
}
.about-section .about-img .img-box img {
  width: 100%;
  display: block;
}
.about-section .about-content {
  flex: 0 0 66.66%;
  max-width: 66.66%;
  padding: 0 15px;
}
.about-section .about-content p {
  color: #555;
  line-height: 26px;
}
.about-section .about-content .stats .stat-box {
  flex: 0 0 25%;
  max-width: 25%;
  padding: 0 15px;
  text-align: center;
  margin-top: 30px;
}
.about-section .about-content .stats .stat-box h2 {
  font-size: 40px;
  color: #000;
  margin-bottom: 5px;
}
.about-section .about-content .stats .stat-box h5 {
  font-size: 16px;
  color: #555;
  font-weight: 600;
  text-transform: uppercase;
}
.about-section .about-content .btn {
  margin-top: 30px;
}
.about-section .section-title {
  margin-bottom: 30px;
  padding: 0;
}

/**** SERVICE SECTION ****/
.service-section {
  padding: 80px 0 0;
}
.service-section .service-item {
  flex: 0 0 33.33%;
  max-width: 33.33%;
  padding: 15px;
}
.service-section .service-item .service-item-inner {
  position: relative;
  overflow: hidden;
}
.service-section .service-item .service-item-inner img {
  width: 100%;
  display: block;
  filter: grayscale(100%);
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.service-section .service-item .service-item-inner:hover img {
  filter: grayscale(0);
  transform: scale(1.1);
}
.service-section .service-item .service-item-inner .overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.service-section .service-item .service-item-inner .overlay h4 {
  color: #fff;
  font-size: 20px;
  text-align: center;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.service-section .service-item .service-item-inner .overlay:hover h4 {
  transform: translateX(30px);
  opacity: 0;
}
.service-section .service-item .service-item-inner:hover .overlay {
  background-color: transparent;
}

/**** WORK SECTION ****/
.work-section {
  padding: 80px 0 0px;
}
.work-section .work-item {
  flex: 0 0 33.33%;
  max-width: 33.33%;
  padding: 15px;
}
.work-section .work-item .work-item-inner {
  position: relative;
  cursor: pointer;
  overflow: hidden;
}
.work-section .work-item .work-item-inner img {
  width: 100%;
  display: block;
  transform: scale(1.1);
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.work-section .work-item .work-item-inner:hover img {
  filter: grayscale(100%);
  transform: scale(1);
}
.work-section .work-item .work-item-inner .overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  padding: 15px;
  display: flex;
  align-items: flex-end;
  opacity: 0;
}
.work-section .work-item .work-item-inner .overlay h4 {
  color: #fff;
  font-size: 20px;
  transform: translateX(30px);
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.work-section .work-item .work-item-inner:hover .overlay {
  opacity: 1;
}
.work-section .work-item .work-item-inner:hover .overlay h4 {
  transform: translateX(0);
}

/**** CONTACT SECTION ****/
.contact-section {
  padding: 80px 0 80px;
}
.contact-section .contact-img {
  width: 100%;
  padding: 0 15px;
}
.contact-section .contact-img .img-box {
  padding: 200px 0;
  background-image: url("/src/img/contact-bg.jpg");
  background-position: 50% 35%;
  background-size: cover;
}
.contact-section .contact-img .section-title {
  text-align: center;
}
.contact-section .contact-img .section-title h1 {
  color: #fff;
}
.contact-section .contact-img .section-title h1::before {
  background-color: #fff;
  left: 50%;
  margin-left: -40px;
}
.contact-section .contact-info {
  flex: 0 0 100%;
  max-width: 100%;
}
.contact-section .contact-info .info-item {
  flex: 0 0 33.33%;
  max-width: 33.33%;
  text-align: center;
  padding: 0 15px;
  margin: 30px 0;
}
.contact-section .contact-info .info-item h5 {
  font-size: 16px;
  font-weight: 600;
}
.contact-section .contact-info .info-item p {
  color: #555;
}
.contact-section .social-links {
  flex: 0 0 100%;
  max-width: 100%;
  padding: 0 15px;
  text-align: center;
}
.contact-section .social-links a {
  margin: 0 5px;
}
.contact-section .social-links a:hover img {
  opacity: 0.5;
}
.contact-section .social-links img {
  width: 28px;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}

.copyright {
  padding: 25px 15px;
  text-align: center;
  color: #555;
  font-size: 15px;
  border-top: 1px solid #c5c5c5;
}

.shape-01 {
  position: absolute;
  width: 30px;
  height: 30px;
  border: 2px solid #ff9800;
  right: 5%;
  top: 20%;
  animation: spin 5s linear infinite;
}

.shape-02 {
  position: absolute;
  width: 30px;
  height: 30px;
  border: 2px solid #ff9800;
  left: 30px;
  top: 30px;
  animation: spin 5s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.scroll-down {
  position: absolute;
  left: 50%;
  bottom: 30px;
  width: 30px;
  height: 30px;
  margin-left: -15px;
  animation: scrollDown 2s ease infinite;
}
.scroll-down img {
  width: 25px;
  display: block;
  margin: 2px auto;
}

@keyframes scrollDown {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-30px);
  }
  60% {
    transform: translateY(-15px);
  }
}
.lightbox {
  position: fixed;
  z-index: 1099;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: zoom-out;
  display: none;
}
.lightbox.open {
  display: flex;
}
.lightbox .lightbox-img {
  max-width: 100%;
  display: flex;
  width: auto;
  height: auto;
  padding: 40px 0;
}
.lightbox .img-box {
  position: relative;
  cursor: auto;
}
.lightbox .img-box .lightbox-caption {
  position: absolute;
  left: 0;
  bottom: 10px;
  color: #fff;
  font-weight: 600;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.lightbox .img-box .lightbox-caption .lightbox-category {
  padding-right: 50px;
}
.lightbox .lightbox-close {
  height: 30px;
  width: 30px;
  display: block;
  position: absolute;
  right: 0;
  top: 10px;
  font-size: 30px;
  color: #fff;
  text-align: center;
  line-height: 26px;
  cursor: zoom-out;
}
.lightbox .lightbox-controls .prev,
.lightbox .lightbox-controls .next {
  position: absolute;
  height: calc(100% - 80px);
  top: 40px;
  cursor: pointer;
  display: flex;
  align-items: center;
}
.lightbox .lightbox-controls .prev {
  left: 0;
  width: 50%;
  justify-content: flex-start;
  padding-left: 5px;
}
.lightbox .lightbox-controls .next {
  right: 0;
  width: 50%;
  justify-content: flex-end;
  padding-right: 5px;
}
.lightbox .lightbox-controls .prev img,
.lightbox .lightbox-controls .next img {
  width: 30px;
  height: 30px;
  padding: 5px;
  border-radius: 2px;
  transition: all 0.5s;
}
.lightbox .lightbox-controls .prev img:hover,
.lightbox .lightbox-controls .next img:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: #111;
  z-index: 1099;
  display: flex;
  align-items: center;
  justify-content: center;
  transition-property: all;
  transition-duration: 0.6s;
  transition-timing-function: ease;
}
.preloader.loaded {
  visibility: hidden;
  opacity: 0;
  z-index: -99;
}
.preloader .loader {
  height: 30px;
  width: 30px;
  border: 2px solid #ff9800;
  border-right: 2px solid transparent;
  border-radius: 50%;
  animation: spin 2s linear infinite;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 99;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.header.fixed {
  background-color: #fff;
  position: fixed;
}
.header.fixed .brand-name a {
  color: #000;
}
.header.fixed .nav ul li a {
  color: #000;
}
.header.fixed .nav-toggle {
  border-color: #000;
}
.header.fixed .nav-toggle span,
.header.fixed .nav-toggle span::before,
.header.fixed .nav-toggle span::after {
  background-color: #000;
}
.header .nav-toggle {
  width: 40px;
  height: 34px;
  border: 1px solid #fff;
  border-radius: 3px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
  display: none;
}
.header .nav-toggle span {
  height: 1px;
  width: 16px;
  display: block;
  background-color: #fff;
  position: relative;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.header .nav-toggle span::before, .header .nav-toggle span::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 1px;
  width: 100%;
  background-color: #fff;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.header .nav-toggle span::before {
  transform: translateY(-6px);
}
.header .nav-toggle span::after {
  transform: translateY(6px);
}
.header .brand-name {
  padding: 0 15px;
}
.header .brand-name a {
  font-size: 30px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 900;
  letter-spacing: 2px;
  transition-property: all;
  transition-duration: 0.5s;
  transition-timing-function: ease;
}
.header .nav {
  padding: 0 15px;
}
.header .nav ul li {
  display: inline-block;
  margin-left: 30px;
}
.header .nav ul li a {
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  display: block;
  padding: 25px 0;
  text-transform: capitalize;
  transition: all 0.5s ease;
}
.header .nav ul li a:hover {
  color: #ff9800;
}

.contact-form {
  padding: 0 65px;
  width: 100%;
  max-width: 100%;
}
.contact-form .form {
  background-color: #f3f2f1;
  padding: 55px 40px;
  margin-top: -150px;
}
.contact-form .form label {
  color: #000;
  font-weight: 500;
  display: block;
  margin-bottom: 5px;
}
.contact-form .form .input-group {
  flex: 0 0 100%;
  max-width: 100%;
  padding: 0 15px;
  margin-bottom: 30px;
}
.contact-form .form .input-group.left, .contact-form .form .input-group.right {
  flex: 0 0 50%;
  max-width: 50%;
}
.contact-form .form .input-control {
  width: 100%;
  height: 45px;
  padding: 6px 0;
  color: #000;
  font-size: 16px;
  font-family: "Raleway", sans-serif, sans-serif;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #ccc;
}
.contact-form .form .input-control:focus {
  outline: none;
}
.contact-form .form textarea.input-control {
  height: 140px;
  resize: none;
  padding-top: 12px;
}
.contact-form .form .form-btn {
  text-align: center;
}

@media (min-width: 776px) {
  .header .nav {
    display: block;
  }
}
@media (max-width: 775px) {
  .header {
    padding: 7px 0;
    position: fixed;
  }
  .header .nav-toggle {
    display: flex;
    margin-right: 15px;
  }
  .header .nav {
    flex: 0 0 100%;
    max-width: 100%;
    background-color: #fff;
    padding: 0;
    display: none;
  }
  .header .nav ul li {
    display: block;
    margin-right: 25px;
  }
  .header .nav ul li a {
    color: #000;
    padding: 15px 0;
  }
  .header.fixed .nav {
    border-top: 1px solid #ccc;
  }
  .header.fixed ul li {
    display: block;
    margin-right: 25px;
  }
  .header.fixed ul li a {
    color: #000;
    padding: 15px 0;
  }

  .home-section {
    background-position: 65%;
    background-size: center;
  }
  .home-section .home-content h1 {
    font-size: 40px;
  }

  .about-section .about-img,
.about-section .about-content {
    flex: 0 0 100%;
    max-width: 100%;
  }

  .about-section .about-img {
    margin-bottom: 30px;
  }

  .service-section .service-item,
.work-section .work-item {
    flex: 0 0 50%;
    max-width: 50%;
  }
}
@media (max-width: 575px) {
  .home-section .home-content h1 {
    font-size: 25px;
    letter-spacing: 5px;
  }

  .about-section .about-content .stats .stat-box {
    flex: 0 0 50%;
    max-width: 50%;
  }

  .service-section .service-item,
.work-section .work-item,
.contact-form .form .input-group.left,
.contact-form .form .input-group.right,
.contact-section .contact-info .info-item {
    flex: 0 0 100%;
    max-width: 100%;
  }

  .contact-form {
    padding: 0 35px;
  }

  .contact-form .form {
    padding: 55px 5px;
  }
}